import ControlsItem from "./ControlsItem"
import s from "./Controls.module.css"
import adaptive from "../SidebarsAdaptive.module.css"
import React from "react"

class Controls extends React.Component {
   constructor(props) {
      super(props);
      this.state = {
         controls: [
            "Profile",
            "News",
            "Messages",
            "Friends",
            "Communities",
            "Settings",
         ]
      }
   }

   render() {
      return (
         // Класс sidebar-wrapper имеет z-index, вынесен в общий файл App.css
         <aside className={`${s.controls} controls`}>
            <ul className={`${s.controlsList} ${adaptive.controlsList} controls-list`}>
               {this.state.controls.map(
                  item => (<ControlsItem key={item} text={item} to={`/${item}`} />)
               )}
            </ul>
         </aside>
      )
   }

}

export default Controls