import PostAuthor from "./PostAuthor/PostAuthor"
import s from "./PostItem.module.css";
import ButtonsItemContainer from "./ButtonsItemContainer";
import PostImage from "../../../../generic/PostImage";

const PostItem = ({ state, callbacks }) => {
   return (
      <div className={`${s.postItem} post`}>
         <PostAuthor avatar={state.postAuthor.avatar} name={state.postAuthor.name} date={callbacks.getFormattedDate(state.post.date, "posts")} />
         <div className={s.postText}>{state.post.text}</div>
         {/* <div className={`${s.postImage} post-image`}> */}
            {state.post.images.map(image => (
               <PostImage key={callbacks.getImageId(image)} src={image} wrapperStyle={{}} />  // max-height: 50vh;
            ))}
         {/* </div> */}
         <div className={s.postButtons}>
            <ButtonsItemContainer key={"like"} attributes={{ className: s.like, image: state.icons.like, value: state.post.likes }} />
            <ButtonsItemContainer key={"comment"} attributes={{ className: s.comment, image: state.icons.comment, value: state.post.comments.length }} />
            <ButtonsItemContainer key={"repost"} attributes={{ className: s.repost, image: state.icons.repost, value: state.post.reposts }} />
         </div>
      </div>
   )
}

export default PostItem