import { fromJS, Map } from "immutable";
import { data } from "../data/main";


const setSubscriptionStatus = (stateMap, action) => {
   const state = stateMap.toJS();
   const myProfileId = state.myProfile;
   const status = action.status ? "unsubscribe": "subscribe";
   const communityId = action.communityId;
   let returnedStateMap = stateMap;

   if (status === "subscribe") {
      returnedStateMap = returnedStateMap.setIn(["communities", communityId, "subscribers"], [...state.communities[communityId].subscribers, myProfileId])
      returnedStateMap = returnedStateMap.setIn(["users", myProfileId, "subscriptions"], [...state.users[myProfileId].subscriptions, communityId])
   } else if (status === "unsubscribe") {
      returnedStateMap = returnedStateMap.setIn(["communities", communityId, "subscribers"], [...state.communities[communityId].subscribers.filter(subscriber => subscriber !== myProfileId)])
      returnedStateMap = returnedStateMap.setIn(["users", myProfileId, "subscriptions"], [...state.users[myProfileId].subscriptions.filter(subscription => subscription !== communityId)])
   }

   return returnedStateMap
}

const initialState = fromJS(
   {
      myProfile: data.myProfile,
      tags: data.entities.tags,
      tagsIds: data.result.tags,
      communities: data.entities.communities,
      communitiesIds: data.result.communities,
      friendsIds: data.result.friends,
      users: data.entities.users,
      usersIds: data.result.users,
   }
);


const sidebarReducer = (state = initialState, action) => {
   let stateMap = !Map.isMap(state) ? fromJS(state) : state;

   // ! Даже при загрузке приложения, в него поступает новый state
   // Это возможно из-за перерисовки всего / многих элементов DOM

   switch (action.type) {
      case "SET-SUBSCRIPTION-STATUS":
         return setSubscriptionStatus(stateMap, action).toJS();

      default:
         return stateMap.toJS();
   }
}


export default sidebarReducer