import React from "react"
import Image from "../../generic/Image";
import Input from "../../generic/Input"
import s from "./HeaderSearch.module.css"

class HeaderSearch extends React.Component {

   constructor(props) {
      super(props);
      this.textInput = React.createRef();

      this.state = {text: ""};
   }
   
   changeInputText = text => {
      this.setState({text: text});
   }

   componentDidMount () {
      this.changeInputText("");
   }

   render () {
      return (
         <div className={`${s.headerSearch} search`}>
            <form className={s.searchForm}>
               <Image src={this.props.searchIcon} wrapperStyle={{width: "1.6rem", height:"1.6rem"}} />
               <Input
                  inputState={this.props.inputState}
                  onInputHandler={() => this.changeInputText(this.textInput.current.value)}
                  refContent={this.textInput}
                  value={this.state.text}
               />
            </form>
         </div>
      )
   }

}

export default HeaderSearch