import React from 'react'
import s from './YouCanKnowThem.module.css'
import adaptive from '../SidebarsAdaptive.module.css';
import YouCanKnowThemItem from './YouCanKnowThemItem';

class YouCanKnowThem extends React.Component {
   constructor(props) {
      super(props);
   }

   componentDidMount() {
      this.props.callbacks.setHoverClass()
   }

   render() {
      return (
         <div className={`${s.youCanKnowThem} ${adaptive.youCanKnowThem} friends-online`}>
            <div className={`${s.youCanKnowThemContainer} friends-online-container`}>
               {this.props.youCanKnowThem.length ? <h3 className={`${s.title} ${adaptive.youCanKnowThemTitle}`}>You can know them</h3> : ""}
               {
                  this.props.youCanKnowThem.slice(0, 6).map(youCanKnowThemItem => {
                     return (
                        <YouCanKnowThemItem key={youCanKnowThemItem.id} youCanKnowThemItem={youCanKnowThemItem} />
                     )
                  }
                  )
               }

               {
                  this.props.youCanKnowThem.length <= 6 ? "" :
                     (<div className={`${s.showMore} ${adaptive.showMore}`}>
                        Show more
                     </div>)
               }
            </div>
         </div>
      )
   }
}

export default YouCanKnowThem