import React from "react";
import s from "./ChatFolder.module.css"
import ChatMessageContainer from "./ChatMessage/ChatMessageContainer";
import ChatMessageDate from "./ChatMessage/ChatMessageDate";
import SendFirstMessage from "./SendFirstMessage";

class ChatFolder extends React.Component {
   constructor(props) {
      super(props);
   }

   getDateObj = dateStr => this.props.callbacks.getDateObj(dateStr);

   getAnswerItfirstMessageOfTheDay = (messageDate, previousMessageDate) => this.props.callbacks.getAnswerItfirstMessageOfTheDay(messageDate, previousMessageDate)
   render() {
      if (!this.props.state.messages.length) return (
         <SendFirstMessage />
      )
      return (
         <div className={`${s.chatFolder} chat-folder`}>

            {this.props.state.messages.map((message, i, messages) =>
            (<div key={message.date}>
               {
                  this.getAnswerItfirstMessageOfTheDay(message.date, i > 0 ? messages[i - 1].date : false) ?
                     <ChatMessageDate date={this.getDateObj(message.date).date} />
                     : ""
               }
               <ChatMessageContainer
                  dialogId={this.props.state.dialogId}
                  message={message} previousMessage={i > 0 ? messages[i - 1] : false}
                  lastMessage={i === messages.length - 1 ? true : false}
               />
            </div>)
            )}

         </div>
      )
   }
}

export default ChatFolder