import { fromJS } from "immutable";
import { connect } from "react-redux"
import Posts from "./Posts"

const mapStateToProps = (state, ownProps) => {
   const myProfileId = state.main.myProfile;
   const myProfile = state.main.entities.users[myProfileId];
   const myProfilePosts = myProfile.posts.map(postId => state.main.entities.posts[postId]);
   const getTime = time => new Date(time).getTime();
   const sortedPosts = fromJS(myProfilePosts).sortBy(post => getTime(post.get("date"))).toJS();

   return {
      posts: sortedPosts,
   }
}

export default connect(mapStateToProps, null)(Posts)