import React from "react";
import FindFriends from "./FindFriends";
import FriendsItemContainer from "./FriendsItemContainer";
import s from "./FriendsList.module.css"
import NoMatchingFriendsFound from "./NoMatchingFriendsFound";

class FriendsList extends React.Component {
   constructor(props) {
      super(props);
   }

   componentDidMount () {
   }

   render() {
      if (!this.props.state.users.length) {
         return <FindFriends />
      }

      if (this.props.state.users.length=== 1 && this.props.state.users[0] === "No matching friends found.") {
         return (<NoMatchingFriendsFound />)
      }

      return (
         <div className={s.friendsList}>
            {this.props.state.users.slice(0).reverse().map((user, i) => {
               return (
                  <FriendsItemContainer
                     key={user.id}
                     countedProps={
                        {
                           userNumber: i,  // *
                           lastNumber: this.props.state.users.length - 1,   // *
                        }
                     }
                     userId={user.id}
                  />
               )
            })
            }
         </div>
      )
   }
}

export default FriendsList