import { connect } from "react-redux"
import createPostId from "../../../../../lib/createId/createPostId";
import { addNewPostActionCreator } from "../../../../../redux/actionCreators/mainActionCreators"
import NewPostForm from "./NewPostForm"

const mapStateToProps = (state, ownProps) => {
   const myProfileId = state.main.myProfile;
   const myProfile = state.main.entities.users[myProfileId];

   return {
      state: {
         myProfileId: myProfileId,
         avatar: myProfile.avatar,
         icons: state.main.icons.newPost,
         textareaState: {
            style: {
               margin: "1.5rem 0",
               padding: "0 0 0 2rem",
               height: "1.5rem",
               width: "90%",
            },
            placeholder: "What's happening?",
         }
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         addNewPost: (text, myProfileId) => {
            let action = addNewPostActionCreator(text, myProfileId);
            dispatch(action);
         },

         changeWindowSize: elem => {
            if (elem.scrollHeight < 210) {
               elem.style.height = "0px";
               elem.style.height = `${elem.scrollHeight}px`;
               elem.style.overflow = "hidden"
            } else {
               elem.style.overflow = "auto"
            }
         },

         getIconKey: pathToIcon => pathToIcon.split("/").join(""),
      }
   }
}

const NewPostFormContainer = connect(mapStateToProps, mapDispatchToProps)(NewPostForm)

export default NewPostFormContainer