import { fromJS } from "immutable";
import { connect } from "react-redux"
import getFormattedDate from "../../../lib/createCivilDate/getFormattedDate";
import ChatFolder from "./ChatFolder"
import getAnswerItFirstMessageOfTheDay from "./chatFunctions/getAnswerItFirstMessageOfTheDay";

const mapStateToProps = (state, ownProps) => {
   const dialog = state.main.entities.dialogs[ownProps.dialogId];
   // Отрисовка в порядке даты последнего сообщения
   const getTime = time => new Date(time).getTime();
   const sortedMessages = fromJS(dialog.messages).sortBy(message => getTime(message.get("date"))).toJS()

   return {
      state: {
         firstMessage: dialog.messages[0],
         dialogId: ownProps.dialogId,
         messages: sortedMessages,
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         getDateObj: (dateStr) => {
            return getFormattedDate(dateStr, "message");
         },
         getAnswerItfirstMessageOfTheDay: (messageDate, previousMessageDate) => getAnswerItFirstMessageOfTheDay(messageDate, previousMessageDate),
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChatFolder);