import { fromJS, Map } from "immutable";
import createPostId from "../../lib/createId/createPostId";
import editAndCheckSearchText from "../../lib/editAndCheckSearchText";
import data from "../data/main";


// * messages, chat
const addNewMessage = (stateMap, action) => {
   let nowStr = new Date().getTime();
   let state = stateMap.toJS();

   let newMessageObj = {
      ...state.messenger.newMessage,
      date: nowStr,
      person: action.person,
      text: action.text,
   };

   let dialogs = state.entities.dialogs;
   // let number = (dialogs.length - 1) - action.number;
   let dialogObj = dialogs[action.dialogId];
   dialogObj.messages.push(newMessageObj);
   // debugger
   //    dialogs.push(dialogObj);

   return fromJS(state);
}

const changeDialogText = (stateMap, action) => {
   return stateMap.setIn(["content", "messenger", "dialogText"], action.text);
}

// ? message search
const changeMessageSearchText = (stateMap, action) => {
   return stateMap.setIn(["messenger", "searchText"], action.text);
}

const changeMessageSearchStatus = (stateMap, action) => {
   let newSearchStatus = editAndCheckSearchText(stateMap.getIn(["messenger", "searchText"])).length ?
      true
      : false;
   return stateMap.setIn(["messenger", "searchStatus"], newSearchStatus);
}

const saveSoughtMessage = (stateMap, action) => {
   console.log(action.soughtMessage)
   return stateMap.setIn(["messenger", "soughtMessage"], { ...action.soughtMessage });
}

// ? friend search
const changeFriendSearchText = (stateMap, action) => {
   return stateMap.setIn(["friendSearch", "searchText"], action.text);
}

const changeFriendSearchStatus = (stateMap, action) => {
   let newSearchStatus = editAndCheckSearchText(stateMap.getIn(["friendSearch", "searchText"])).length ?
      true
      : false;
   return stateMap.setIn(["friendSearch", "searchStatus"], newSearchStatus);
}

const saveSoughtFriend = (stateMap, action) => {
   console.log(action.soughtMessage)
   return stateMap.setIn(["friendSearch", "soughtMessage"], { ...action.soughtMessage });
}

// * Profile
const addNewPost = (stateMap, action) => {
   debugger
   const state = stateMap.toJS();
   const postId = createPostId(action.myProfileId);

   let newPostObj = {
      ...state.newPost,
      date: new Date().getTime(),
      text: action.text,
      author: action.myProfileId,
      id: postId,
   };

   state.entities.posts[postId] = { ...newPostObj };
   state.entities.users[state.myProfile].posts.push(postId)

   return fromJS(state);
}

// * Friends
const setUserCategory = (stateMap, action) => (
   stateMap.setIn(["userCategories", "currentCategory"], action.categoryTitle)
)

const setUserCategoryChangeCompleted = (stateMap, action) => (
   stateMap.setIn(["userCategories", "categoryChangeCompleted"], action.changeStatus)
)

const setUserStatus = (stateMap, action) => {
   const state = stateMap.toJS();
   const myProfileId = state.myProfile;
   const status = action.status ? "unfriend": "add";
   const userId = action.userId;
   let returnedStateMap = stateMap;

   if (status === "add") {
      returnedStateMap = returnedStateMap.setIn(["entities", "users", userId, "friends"], [...state.entities.users[userId].friends, myProfileId])
      returnedStateMap = returnedStateMap.setIn(["entities", "users", myProfileId, "friends"], [...state.entities.users[myProfileId].friends, userId])
   } else if (status === "unfriend") {
      returnedStateMap = returnedStateMap.setIn(["entities", "users", userId, "friends"], [...state.entities.users[userId].friends.filter(friend => friend !== myProfileId)])
      returnedStateMap = returnedStateMap.setIn(["entities", "users", myProfileId, "friends"], [...state.entities.users[myProfileId].friends.filter(friend => friend !== userId)])
   }

   return returnedStateMap
}


const initialState = fromJS({
   icons: {
      newPost: data.icons.newPost.attachMedia,
      chat: data.icons.chat,
      search: data.icons.search,
      postActivity: data.icons.postActivity
   },

   myProfile: data.myProfile,

   newPost: data.newPost,

   messenger: data.messenger,

   friendSearch: data.friendSearch,

   entities: data.entities,

   result: data.result,

   userCategories: data.userCategories,
})

const mainReducer = (state = initialState, action) => {
   let stateMap = !Map.isMap(state) ? fromJS(state) : state;

   switch (action.type) {
      // * messages, chat
      case "ADD-NEW-MESSAGE":
         // try {
         //    console.log(stateMap.toJS()["UI"]["messages"] === addNewMessage(stateMap, action).toJS()["UI"]["messages"]);
         //    console.log(stateMap.toJS()["UI"]["messages"], addNewMessage(stateMap, action).toJS()["UI"]["messages"]);
         // } catch (err) {
         //    console.log(err.name)
         // }
         let newState = addNewMessage(stateMap, action).toJS()
         console.log(newState)
         return newState;
      // return addNewMessage(stateMap, action).toJS();

      case "CHANGE-DIALOG-TEXT":
         return changeDialogText(stateMap, action).toJS();

      // ? message search
      case "CHANGE-MESSAGE-SEARCH-TEXT":
         return changeMessageSearchText(stateMap, action).toJS();

      case "CHANGE-MESSAGE-SEARCH-STATUS":
         return changeMessageSearchStatus(stateMap, action).toJS();

      case "SAVE-SOUGHT-MESSAGE":
         return saveSoughtMessage(stateMap, action).toJS();

      // ? friend search
      case "CHANGE-FRIEND-SEARCH-TEXT":
         return changeFriendSearchText(stateMap, action).toJS();

      case "CHANGE-FRIEND-SEARCH-STATUS":
         return changeFriendSearchStatus(stateMap, action).toJS();

      case "SAVE-SOUGHT-FRIEND":
         return saveSoughtFriend(stateMap, action).toJS();

      // * Profile
      case "ADD-NEW-POST":
         return addNewPost(stateMap, action).toJS();

      // * Friends
      case "SET-FRIENDS-CATEGORY":
         return setUserCategory(stateMap, action).toJS();

      case "SET-USER-CATEGORY-CHANGE-COMPLETED":
         return setUserCategoryChangeCompleted(stateMap, action).toJS();

      case "SET-USER-STATUS":
         return setUserStatus(stateMap, action).toJS();

      default:
         return stateMap.toJS();
   }
}

export default mainReducer