import { NavLink } from "react-router-dom"
import Avatar from "../../generic/Avatar"
import s from "./ChatHeader.module.css"

const ChatHeader = ({ interlocutor, icons }) => (
   <div className={`${s.header} chat-header`}>
      <NavLink className={s.navLink} to="/Messages">
         <button className={s.backButton}>
            <img className={s.backArrow} src={icons.arrow} alt="back" />
            <div className={s.backButtonText}>Back</div>
         </button>
      </NavLink>
      <div className={s.interlocutorName}>
         {interlocutor.name}
      </div>
      <Avatar src={interlocutor.avatar} wrapperStyle={{cursor: "pointer", width: "3rem", height: "3rem"}} />
      <div className={`${s.otherIcon} messages-icon-other`}>
         <img src={icons.other} alt="create a group chat" />
      </div>
   </div>
)

export default ChatHeader