import { connect } from "react-redux";
import callSetClassForHoverEv from "../../../lib/callSetClassForHoverEv";
import WhoToFollow from "./WhoToFollow";

const mapStateToProps = (state, ownProps) => {
   const communities = state.sidebar.communities;
   const communitiesIds = state.sidebar.communitiesIds;
   return {
      communities: communitiesIds.map(communityId => {
         return communities[communityId]
      }),
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      setHoverClass: () => {
         callSetClassForHoverEv("Profile", "who-to-follow-item-avatar--active", "who-to-follow-avatar", "who-to-follow-item");
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(WhoToFollow);