import React from "react";
import Avatar from "../../../../generic/Avatar";
import Textarea from "../../../../generic/Textarea";
import s from "./NewPostForm.module.css";
import NewPostIconContainer from "./NewPostIconContainer";

class NewPostForm extends React.Component {
   constructor(props) {
      super(props)
      this.state = {
         text: "",
      }

      this.style = {
         gridArea: "avatar",
         width: "4.5rem",
         height: "4.5rem",
      }
   }

   changeTextareaText = text => {
      this.setState({
         text: text,
      })
   }

   getTextareaCallbacks = () => (
      {
         changeText: this.changeTextareaText,
         changeWindowSize: this.props.callbacks.changeWindowSize,
      }
   )

   buttonClickHandler = () => {
      debugger
      this.props.callbacks.addNewPost(this.state.text, this.props.state.myProfileId);
      this.changeTextareaText("");
   }

   getIconKey = text => this.props.callbacks.getIconKey(text)

   render() {
      return (
         <form className={s.form}>
            <Avatar src={this.props.state.avatar} wrapperStyle={this.style} />

            <div className={s.input}>
               <Textarea
                  textareaState={this.props.state.textareaState}
                  value={this.state.text}
                  callbacks={this.getTextareaCallbacks()}
               />
               <div className={s.icons}>
                  {this.props.state.icons.map(
                     (icon, i) => {
                        return (
                           <NewPostIconContainer key={this.getIconKey(icon)} number={i} icon={icon} />
                        )
                     }
                  )}
               </div>
            </div>
            <button type="button" onClick={this.buttonClickHandler} className={s.button}>Send</button>
         </form>
      )
   }
}

export default NewPostForm