import { connect } from "react-redux"
import setPaddingRight from "../../../../lib/setPaddingRight";
import setSidebarPositionAndSize from "../../../../lib/setSidebarPosition/setSidebarPositionAndSize";
import { setUserCategoryChangeCompletedActionCreator, setUserStatusActionCreator } from "../../../../redux/actionCreators/mainActionCreators";
import FriendsItem from "./FriendsItem"

const mapStateToProps = (state, ownProps) => {
   const user = state.main.entities.users[ownProps.userId];
   const myProfile = state.main.entities.users[state.main.myProfile]

   return {
      state: {
         user: user,
         userNumber: ownProps.countedProps.userNumber,
         lastNumber: ownProps.countedProps.lastNumber,
         userCategoryChangeCompleted: state.main.userCategories.categoryChangeCompleted,
         myProfile: myProfile,
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         setMountStyles: () => {
            setSidebarPositionAndSize("fixed", "Friends");
            setPaddingRight();
         },
         completeChangingTheCategoryOfUsers: () => {
            const action = setUserCategoryChangeCompletedActionCreator(true);
            dispatch(action)
         },
         startChangingTheCategoryOfUsers: () => {
            const action = setUserCategoryChangeCompletedActionCreator(false);
            dispatch(action)
         },
         getAddButtonText: (user, myProfile) => (
            myProfile.friends.includes(user.id) ? "Unfriend" : "Add"
         ),
         addButtonClickHandler: (user, myProfile) => {
            const status = myProfile.friends.includes(user.id) ? true : false;
            const action = setUserStatusActionCreator(status, user.id);
            dispatch(action);
         },
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(FriendsItem)