import { connect } from "react-redux"
import UserAvatar from "./UserAvatar"

const mapStateToProps = (state) => {
   const users = state.main.entities.users;
   const myProfileId = state.main.myProfile;

   return {
      avatar: users[myProfileId].avatar,
   }
}

const UserAvatarContainer = connect(mapStateToProps, null)(UserAvatar);

export default UserAvatarContainer