import React from "react";
import setPaddingRight from "../../../../lib/setPaddingRight";
import s from "./SendFirstMessage.module.css"

class sendFirstMessage extends React.Component {
   constructor (props) {
      super(props);
   }

   componentDidMount () {
      setPaddingRight()
   }

   render () {
      return (
         <div className={s.sendFirstMessage}>
            Send the first message
         </div>
      )
   }
}

export default sendFirstMessage