import { connect } from "react-redux"
import getFormattedDate from "../../../../lib/createCivilDate/getFormattedDate";
import scrollForSoughtMessageAndRemoveSoughtClass from "../../../../lib/setStyleForSearchedMessage/scrollForSoughtMessageAndRemoveSoughtClass"
import chatFolderScroll from "../chatFolderScroll";
import getAnswerItFirstMessageOfTheDay from "../chatFunctions/getAnswerItFirstMessageOfTheDay";
import ChatMessage from "./ChatMessage"

const mapStateToProps = (state, ownProps) => {
   const getTime = dateStr => new Date(dateStr).getTime();
   const dialog = state.main.entities.dialogs[ownProps.dialogId];
   const myProfileId = state.main.myProfile;
   const myProfile = state.main.entities.users[myProfileId];
   const interlocutorId = dialog.authors.filter(author => author !== myProfileId)[0];
   const interlocutor = state.main.entities.users[interlocutorId]
   const messageAuthor = ownProps.message.person === interlocutorId ? interlocutor : myProfile;
   const changeAuthor = ownProps.message.person !== ownProps.previousMessage.person ? true : false;
   const dateObj = getFormattedDate(ownProps.message.date, "message");
   const timeDifferent = ownProps.previousMessage ?
      (getTime(ownProps.message.date) - getTime(ownProps.previousMessage.date))
      : ((15 + 1) * 60 * 1000);

   let renderMessage;
   if (getAnswerItFirstMessageOfTheDay(ownProps.message.date, ownProps.previousMessage.date)
      || changeAuthor) {
      renderMessage = "start";
   } else {
      renderMessage = "continuation";
   }

   let searchClassName;
   if (
      state.main.messenger.searchStatus
      && state.main.messenger.soughtMessage.message.text === ownProps.message.text
      && state.main.messenger.soughtMessage.message.date === ownProps.message.date
   ) {
      searchClassName = "sought";
   } else {
      searchClassName = "outOfSearch";
   }

   const time = (
      renderMessage === "start"
      || (timeDifferent > (15 * 60 * 1000))
   ) ?
      dateObj.time
      : false;

   return {
      state: {
         startMessageState: {
            searchClassName: searchClassName,
            avatar: messageAuthor.avatar,
            name: messageAuthor.name,
            time: time,
            text: ownProps.message.text,
         },
         continuationMessageState: {
            searchClassName: searchClassName,
            text: ownProps.message.text,
         },
         dateObj: getFormattedDate(ownProps.message.date, "message"),
         renderMessage: renderMessage,
         time: time,
         lastMessage: ownProps.lastMessage,
         changeAuthor: changeAuthor,
         searchStatus: state.main.messenger.searchStatus,
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         setStyleForSoughtMessage: () => {
            scrollForSoughtMessageAndRemoveSoughtClass();
         },
         callChatFolderScroll: () => chatFolderScroll(),
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChatMessage);