const scrollForSoughtMessageAndRemoveSoughtClass = () => {
   let soughtClass;
   let soughtMessage;
   let soughtMessageSizes;

   try {
      soughtMessage = document.querySelector(".sought");
   } catch (err) {
      console.log(err.message)
   }

   if (!soughtMessage) return;

   soughtMessageSizes = soughtMessage.getBoundingClientRect();

   if (soughtMessage.classList.length > 1) {
      soughtClass = soughtMessage.classList[1].includes("sought") ?
         soughtMessage.classList[1]
         : "undefined"
   }

   if (soughtClass === "undefined") return;

   if (soughtMessageSizes.y < 90) {
      console.log(soughtMessageSizes.y);
      document.documentElement.scrollTop = 70;
   }

   setTimeout(() => {
      soughtMessage.classList.remove(soughtClass)
   }, 3000)
}

export default scrollForSoughtMessageAndRemoveSoughtClass