import s from "./ChatEntryField.module.css"
import Textarea from "../../generic/Textarea";
import React from "react";

// const ChatEntryField = ({dialogText, icons, callbacks}) => {
class ChatEntryField extends React.Component {
   constructor(props) {
      super(props);
      this.state = {
         text: "",
      }
   }

   changeTextareaText = text => {
      this.setState({
         text: text,
      })
   }

   sendMessage = () => {
      this.props.callbacks.sendMessage(this.state.text, this.props.state.person);
      this.changeTextareaText("");
   }

   render() {
      return (
         <form className={s.entryField}>
            <button type="button" className={s.entryFieldButton}>
               <img className={s.attachments} src={this.props.state.icons.attachments} alt="attachments icon" />
            </button>
            <Textarea
               textareaState={this.props.state.textareaState}
               value={this.state.text}
               callbacks={
                  {
                     changeText: this.changeTextareaText,
                     changeWindowSize: this.props.callbacks.changeWindowSize,
                  }
               }
            />
            <button type="button" className={s.entryFieldButton}>
               <img className={s.emoji} src={this.props.state.icons.emoji} alt="emoji icon" />
            </button>
            <button type="button" onClick={this.sendMessage} className={s.entryFieldButton}>
               <img className={s.sendArrow} src={this.props.state.icons.sendArrow} alt="send arrow icon" />
            </button>
            <div className={s.bottom}></div>
         </form>
      )
   }
}

export default ChatEntryField