import s from './WhoToFollowItem.module.css'
import adaptive from '../SidebarsAdaptive.module.css'
import Avatar from '../../generic/Avatar'

const WhoToFollowItem = ({state, callbacks}) => {
   const wrapperStyle = {
      gridArea: "avatar",
      justifySelf: "center",
      alignSelf: "center",
      transitionProperty: "width padding",
      transitionDuration: "0.03s",
      // width и height определяются в App.scss,
      // т.к. их надо переопределять при событии hover
   }

   return (
      <div className={`${s.whoToFollowItem} who-to-follow-item`}>
         <Avatar src={state.community.avatar} wrapperStyle={wrapperStyle} additionalClass={"who-to-follow-avatar"}/>
         <div className={`who-to-follow-community ${s.community}`}>
            {`${state.community.name}`}
            {state.community.check ? <img className={s.check} src="./icons/check.svg" alt="community avatar"/> : ""}
         </div>
         <div className={`who-to-follow-communityLogin ${s.communityLogin}`}>{state.community.login}</div>
         <button
            onClick={() => callbacks.followButtonClickHandler(state.community, state.myProfileId)}
            className={`who-to-follow-button ${s.button} ${adaptive.button}`}
         >
            {callbacks.getFollowButtonText(state.community, state.myProfileId)}
         </button>
      </div>
   )
}

export default WhoToFollowItem