const editAndCheckSearchText = text => {
   let editedAndCheckedText = text;
// debugger
   if (text.length === 0) {
      // * Возврат ""
      editedAndCheckedText = text;
   } else {
      if (text.split("").filter(item => item !== " ").length > 0) {
         let startNumber;
         let endNumber;
         let textWithoutStartAndEndSpaces;

         text.split("").find((item, index) => {
            startNumber = index;
            return item !== " ";
         });

         text.split("").reverse().find((item, index) => {
            endNumber = (text.length) - index;
            return item !== " ";
         });

         // Удаление пробелов вначале и в конце
         textWithoutStartAndEndSpaces = text.slice(startNumber, endNumber)

            // ? \p{Letter} - поиск буквы в любом языке (свойство Юникода)
         if (!textWithoutStartAndEndSpaces.match(/[\p{Letter}\d]/u)) {
            // * Возврат ""
            editedAndCheckedText = "";
            // console.log("В тексте нет букв и цифр", !text.match(/\s/), !text.match(/[\p{Letter}\d]/u));
         } else {
            // * Возврат текста, у которого:
            // * 1) Удалены пробелы в начале и в конце
            // * 2) Заменены несколько пробелов подряд одним пробелом
            // * 3) Не буквенные символы заменяются пробелом
            // * 4) Нижний регистр
            // Возвращает текст, у которого не буквенные символы заменяются пробелом
            editedAndCheckedText = textWithoutStartAndEndSpaces
               .split("")
               .map(item => (!item.match(/[\p{Letter}\d]/u) && !item.match(/\s/) ? " " : item))
               .join("")
               .split(" ")
               .filter(item => item !== "")
               .join(" ")
               .toLowerCase();
         }
      } else {
         // console.log("В тексте одни пробелы");
         // * Возврат ""
         editedAndCheckedText = "";
      }
   }
   return editedAndCheckedText;
}

export default editAndCheckSearchText;