import s from './ButtonsItem.module.css'

const ButtonsItem = ({ state, getActivityHTML }) => (
   <button className={`${state.className} ${s.buttonsItem}`}>
      <div className={`buttons-item-image ${s.image}`}>
         <img src={state.image} alt="" />
      </div>
      {getActivityHTML(state.value, s.value)}
   </button>
)

export default ButtonsItem