import React from "react";
import s from "./ChatContinuationMessage.module.css"

class ChatContinuationMessage extends React.Component {
   constructor(props) {
      super(props);
   }

   render() {
      return (
         <div className={`${s.continuation} ${s[this.props.state.searchClassName]} ${this.props.state.searchClassName}`}>
            <div className={s.text}>
               {this.props.state.text}
            </div>
         </div>
      )
   }
}

export default ChatContinuationMessage