import { fromJS } from "immutable";
import editAndCheckSearchText from "../editAndCheckSearchText";

const getSearchedDialogs = (allDialogsMap, editedAndCheckedSearchText) => {

   // debugger
   const allDialogs = allDialogsMap.toJS();
   const searchedDialogs = allDialogs.filter(dialog => {
      return Boolean(dialog.messages.find(message => (
         editAndCheckSearchText(message.text).includes(editedAndCheckedSearchText)
      )
      ))
   })

   return fromJS(searchedDialogs)
}

export default getSearchedDialogs