import React from "react";
import Avatar from "../../../generic/Avatar";
import s from "./FriendsItem.module.css"

class FriendsItem extends React.Component {
   constructor(props) {
      super(props);
   }

   componentDidMount() {
      if (
         this.props.state.userNumber === this.props.state.lastNumber
         && !this.props.state.userCategoryChangeCompleted
      ) {
         this.setMountStyles();
         this.completeChangingTheCategoryOfUsers();
      };
   }

   componentDidUpdate(prevProps) {
      if (this.props.state.userCategoryChangeCompleted !== prevProps.state.userCategoryChangeCompleted) {
         this.setMountStyles();
         this.completeChangingTheCategoryOfUsers();
      }
   }

   componentWillUnmount() {
      this.startChangingTheCategoryOfUsers();
   }

   setMountStyles = () => {
      this.props.callbacks.setMountStyles();
   }

   completeChangingTheCategoryOfUsers = () => this.props.callbacks.completeChangingTheCategoryOfUsers();

   startChangingTheCategoryOfUsers = () => this.props.callbacks.startChangingTheCategoryOfUsers();

   getButtonText = (user, myProfile) => this.props.callbacks.getAddButtonText(myProfile, user)

   addButtonClickHandler = (user, myProfile) => this.props.callbacks.addButtonClickHandler(user, myProfile)

   render() {

      return (
         <div className={s.friendsItem}>
            <Avatar src={this.props.state.user.avatar} wrapperStyle={{ width: "8rem", height: "8rem", gridArea: "avatar" }} />
            <div className={s.name}>
               {this.props.state.user.name}
            </div>
            <div className={s.education}>
               {this.props.state.user.education}
            </div>
            {this.props.state.user.online ? (<div className={s.online}>online</div>)
               : (<div className={s.offline}>offline</div>)}
            <button className={s.addButton} onClick={() => this.addButtonClickHandler(this.props.state.user, this.props.state.myProfile)}>
               {this.getButtonText(this.props.state.user, this.props.state.myProfile)}
            </button>
         </div>
      )
   }
}

export default FriendsItem