import Image from "../../../../generic/Image"
import s from "./UserAvatar.module.css"

const UserAvatar = ({ avatar }) => {
   const style = {
      overflow: "hidden",
      borderRadius: "0.6rem",
      width: "100%",
      height: "100%",
   }

   return (
      <div className={`${s.userAvatarWrapper} user-avatar-wrapper`}>
         <Image wrapperStyle={style} src={avatar} />
      </div>
   )
}

export default UserAvatar