import s from "./Avatar.module.css"

// * в style указать width и padding
const Avatar = ({ src, wrapperStyle, imgStyle, additionalClass}) => {
   return (
      <div className={`${s.avatar} ${additionalClass}`} style={wrapperStyle}>
         <img src={src} alt="avatar" style={imgStyle} />
      </div>
   )
}

export default Avatar