import { connect } from "react-redux";
import callSetClassForHoverEv from "../../../lib/callSetClassForHoverEv";
import YouCanKnowThem from "./YouCanKnowThem"

const mapStateToProps = (state, ownProps) => {
   const users = state.sidebar.users
   const myProfileId = state.sidebar.myProfile;
   const usersIds = state.sidebar.usersIds;
   const friendsIds = users[myProfileId].friends;
   const youCanKnowThemIds = usersIds.filter((userId) => (userId !== myProfileId && !friendsIds.includes(userId)))
   return {
      youCanKnowThem: youCanKnowThemIds.map(youCanKnowThemItemId => users[youCanKnowThemItemId])
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         setHoverClass: () => {
            callSetClassForHoverEv("Messages", "friends-online-person-avatar--active", "friends-online-person-avatar", "friends-online-item");
         },
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(YouCanKnowThem)