// * messages
export const addNewMessageActionCreator = (dialogId, text, person) => (
   {
      type: "ADD-NEW-MESSAGE",
      dialogId: dialogId,
      text: text,
      person: person,
   }
)

// ? message search
export const changeMessageSearchTextActionCreator = text => (
   {
      type: "CHANGE-MESSAGE-SEARCH-TEXT",
      text: text,
   }
)

export const changeMessageSearchStatusActionCreator = () => (
   {
      type: "CHANGE-MESSAGE-SEARCH-STATUS",
   }
)

export const saveSoughtMessageActionCreator = soughtMessage => (
   {
      type: "SAVE-SOUGHT-MESSAGE",
      soughtMessage: soughtMessage,
   }
)

// ? friend search
export const changeFriendSearchTextActionCreator = text => (
   {
      type: "CHANGE-FRIEND-SEARCH-TEXT",
      text: text,
   }
)

export const changeFriendSearchStatusActionCreator = () => (
   {
      type: "CHANGE-FRIEND-SEARCH-STATUS",
   }
)

export const saveSoughtFriendActionCreator = soughtMessage => (
   {
      type: "SAVE-SOUGHT-FRIEND",
      soughtMessage: soughtMessage,
   }
)

// * profile
export const addNewPostActionCreator = (text, myProfileId ) => (
   {
      type: "ADD-NEW-POST",
      text: text,
      myProfileId: myProfileId,
   }
)

// * friends
export const setUserCategoryActionCreator = categoryTitle => (
   {
      type: "SET-FRIENDS-CATEGORY",
      categoryTitle: categoryTitle,
   }
)

export const setUserCategoryChangeCompletedActionCreator = changeStatus => (
   {
      type: "SET-USER-CATEGORY-CHANGE-COMPLETED",
      changeStatus: changeStatus,
   }
)

export const setUserStatusActionCreator = (status, userId) => (
   {
      type: "SET-USER-STATUS",
      status: status,
      userId: userId,
   }
)