import { connect } from "react-redux"
import ChatHeader from "./ChatHeader"

const mapStateToProps = (state, ownProps) => {
   const dialogId = ownProps.dialogId
   const dialog = state.main.entities.dialogs[dialogId];
   const interlocutorId = dialog.authors.filter(author => author !== state.main.myProfile);

   return {
      interlocutor: state.main.entities.users[interlocutorId],
      icons: state.main.icons.chat,
   }
}

export default connect(mapStateToProps, null)(ChatHeader);