import { connect } from "react-redux"
import { changeMessageSearchTextActionCreator, changeMessageSearchStatusActionCreator } from "../../../../../redux/actionCreators/mainActionCreators";
import MessageSearch from "./MessageSearch";

const mapStateToProps = (state, ownProps) => {
   const searchStatus = state.main.messenger.searchStatus;
   const icons = state.main.icons.search;

   return {
      state: {
         icons: {
            searchIcon: icons.mainSearchIcon,
            options: searchStatus ?
               [false, {icon: icons.hideIcon, role: "hide"}]
               : [{icon: icons.groupChatIcon, role: "createGroupChat"}, {icon: icons.otherIcon, role: "otherOptions"}],
         },
         text: state.main.messenger.searchText,
         searchStatus: searchStatus,
         inputState: {
            placeholder: "Search",
            style: {
               padding: "0 0.2rem",
               fontSize: "var(--main-fz)",
            }
         }
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         changeSearchText: text => {
            let action = changeMessageSearchTextActionCreator(text);
            dispatch(action);
         },
         changeSearchStatus: () => {
            let action = changeMessageSearchStatusActionCreator()
            dispatch(action);
         },
         getIconId: icon => {
            return icon.split("/").join("")
         },
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(MessageSearch);