import React from "react";
import s from "./NewPostIcon.module.css";

class NewPostIcons extends React.Component {
   constructor(props) {
      super(props);
   }

   onloadHandler = () => {
      this.props.callbacks.setSidebarPositionAndSize();
      this.props.callbacks.setPaddingRight();
   }

   render() {
      return (
         <div
            onLoad={() => {
               if (this.props.number === 3) {
                  this.onloadHandler();
               }
            }}
            className={s.iconsItem}
         >
            <img src={this.props.icon} alt="New post icon" />
         </div>
      )
   }
}

export default NewPostIcons;