import { connect } from "react-redux";
import callSetClassForHoverEv from "../../../lib/callSetClassForHoverEv";
import Actual from "./Actual";

const mapStateToProps = (state, ownProps) => {
   console.log(state.sidebar.tags)
   const tags = state.sidebar.tags;

   return {
      tags: state.sidebar.tagsIds.map(tagId => tags[tagId]),
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      setHoverClass: () => {
         callSetClassForHoverEv("Profile", "actual-trend--active", "actual-trend", "actual-item");
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(Actual);