import React from 'react'
import setPaddingRight from '../../../lib/setPaddingRight';
import s from './ErrorNotFound.module.css'

class ErrorNotFound extends React.Component {
   constructor (props) {
      super(props);
   }

   componentDidMount () {
      setPaddingRight()
   }

   render () {
      return (
         <div className={s.errorNotFound}>
            404. Page not found...
         </div>
      )
   }
}

export default ErrorNotFound