import { connect } from "react-redux"
import setPaddingRight from "../../../../../lib/setPaddingRight"
import setSidebarPositionAndSize from "../../../../../lib/setSidebarPosition/setSidebarPositionAndSize"
import NewPostIcons from "./NewPostIcons"

const mapStateToProps = (state, ownProps) => {
   return {
      state: {
         number: ownProps.number,
         icon: ownProps.icon,
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         setSidebarPositionAndSize: () => {
            setSidebarPositionAndSize("fixed", "Profile");
         },
         setPaddingRight: () => {
            setPaddingRight();
         }
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(NewPostIcons)