import React from "react"
import UserCategeoryButtonContainer from "./UserCategeoryButtonContainer";
import s from "./UserCategorySelectionPanel.module.css"

class UserCategorySelectionPanel extends React.Component {
   constructor(props) {
      super(props);

      this.state = {
         categoryTitles: this.props.categoryTitles,
      }
   }

   getButtonKey = categoryTitle => categoryTitle.split(" ").join("")

   render() {
      return (
         <div className={`${s.userCategorySelectionPanel} user-category-selection-panel`} >
            <div className={s.userCategorySelectionPanelWrapper} >
               {this.state.categoryTitles.map(categoryTitle => (
                  <UserCategeoryButtonContainer key={this.getButtonKey(categoryTitle)} categoryTitle={categoryTitle}/>
               )
               )}
            </div>
         </div>
      )
   }
}

export default UserCategorySelectionPanel