import Avatar from "../../../generic/Avatar";
import s from "./ChatStartMessage.module.css";

const ChatStartMessage = ({state}) => (
   <div className={`${s.firstMessageOfTheDay} ${s[state.searchClassName]} ${state.searchClassName}`}>

   <Avatar src={state.avatar} wrapperStyle={{width: "4rem", height: "4rem", gridArea: "avatar"}} />

   <div className={s.name}>
      {state.name}
   </div>
   <div className={s.date}>
      {state.time}
   </div>
   <div className={s.text}>
      {state.text}
   </div>
</div>
)

export default ChatStartMessage