import React from "react"
import s from "./Friends.module.css"
import FriendsListContainer from "./FriendsList/FriendsListContainer"
import FriendSearchContainer from "./FriendsSearch/FriendSearchContainer"
import UserCategorySelectionPanelContainer from "./UserCategorySelectionPanel/UserCategorySelectionPanelContainer"

class Friends extends React.Component {
   constructor(props) {
      super(props);
   }

   componentWillUnmount() {
      this.props.unmountHandler()
   }

   render () {
      return (
         <div className={`${s.friends} friends`} >
            <FriendSearchContainer />
            <UserCategorySelectionPanelContainer />
            <FriendsListContainer />
         </div>
      )
   }
} 

export default Friends