import s from './Messages.module.css'
import React from "react";
import MessageSearchContainer from './Dialogs/MessageSearch/MessageSearchContainer';
import DialogsContainer from "./Dialogs/DialogsContainer";
import zeroingScrollTop from '../../../lib/zeroingScrollTop';
import setPaddingRight from '../../../lib/setPaddingRight';
import mainScroll from '../../../lib/mainScroll';

class Messages extends React.Component {
   constructor(props) {
      super(props);
   }

   componentDidMount() {
      setPaddingRight();
      mainScroll("set");
   }

   componentWillUnmount () {
      mainScroll("save");
      zeroingScrollTop();
   }

   render() {
      return (
         <div className={`${s.messages} messages`}>
            <MessageSearchContainer/>
            <DialogsContainer/>
         </div>
      )
   }
}

export default Messages