import { connect } from "react-redux"
import { setUserCategoryActionCreator, setUserCategoryChangeCompletedActionCreator } from "../../../../redux/actionCreators/mainActionCreators"
import UserCategeoryButton from "./UserCategeoryButton"

const mapStateToProps = (state, ownProps) => {
   const currentCategory = state.main.userCategories.currentCategory;
   const categoryTitle = ownProps.categoryTitle;
   const renderSelectedComponent = currentCategory === categoryTitle ? true: false;

   return {
      state: {
         categoryTitle: categoryTitle,
         renderSelectedComponent: renderSelectedComponent,
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         setUserCategory: categoryTitle => {
            const action = setUserCategoryActionCreator(categoryTitle)
            dispatch(action);
         },
         startChangingTheCategoryOfUsers: () => {
            const action = setUserCategoryChangeCompletedActionCreator(false);
            dispatch(action)
         },
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(UserCategeoryButton)