import { connect } from "react-redux"
import getFormattedDate from "../../../../lib/createCivilDate/getFormattedDate";
import trimMessage from "../../../../lib/trimMessage";
import { saveSoughtMessageActionCreator } from "../../../../redux/actionCreators/mainActionCreators"
import DialogsItem from "./DialogsItem"

const mapStateToProps = (state, ownProps) => {
   // const dialogId = ownProps.dialogId;
   // const dialog = state.main.entities.dialogs[dialogId];
   const dialog = ownProps.dialog;
   const myProfileId = state.main.myProfile;
   const interlocutorId = dialog.authors.filter(author => author !== myProfileId);

   return {
      state: {
         countedProps: ownProps.countedProps,
         interlocutor: state.main.entities.users[interlocutorId],
         dialog: dialog,
         lastMessage: dialog.messages[dialog.messages.length - 1].text,
         dateStr: dialog.messages[dialog.messages.length - 1].date,
         messenger: state.main.messenger,
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         saveSoughtMessage: ({ dialogId, message, soughtMessageObj }) => {
            let soughtMessage = { ...soughtMessageObj };
            soughtMessage.dialogId = dialogId;
            soughtMessage.message = message;
            let action = saveSoughtMessageActionCreator(soughtMessage);
            dispatch(action)
         },
         getFormattedDate: dateStr => getFormattedDate(dateStr, "dialog"),
         trimMessage: message => trimMessage(message),
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(DialogsItem);