const getSidebarContentHeight = mainComponent => {
   let sidebarContentHeight;
   
   if (mainComponent === "Profile") {
      let whoToFollowSizes = document.querySelector(".who-to-follow").getBoundingClientRect();
      sidebarContentHeight = document.documentElement.offsetWidth > 1130 ?
      450 + 130 + whoToFollowSizes.height : 450;
   } else if (mainComponent === "Messages" || mainComponent === "Friends" ) {
      let sidebarSizes;
      if (mainComponent === "Messages") {
         sidebarSizes = document.querySelector(".friends-online-container").getBoundingClientRect();
      } else if (mainComponent === "Friends") {
         sidebarSizes = document.querySelector(".controls-list").getBoundingClientRect();
      }
      let sidebarHeight = sidebarSizes.height + 100;
      let minSidebarFullScroll = 360;
      let maxSidebarFullScroll = sidebarHeight > minSidebarFullScroll ? sidebarHeight : minSidebarFullScroll;
      sidebarContentHeight = document.documentElement.offsetWidth > 1130 ? maxSidebarFullScroll : minSidebarFullScroll;
   }

   return sidebarContentHeight;
}

export default getSidebarContentHeight;