import s from "./Input.module.css"

const Input = ({inputState, value, refContent, onInputHandler}) => {
   return (
      <input
         value={value}
         ref={refContent}
         onInput={onInputHandler}
         className={`${s.input}`}
         type="text"
         style={inputState.style}
         placeholder={inputState.placeholder}
      />
   )
}


export default Input