import { fromJS } from "immutable";
import { connect } from "react-redux"
import getTheFriendsAreYouLookingFor from "../../../../lib/getTheFriendsAreYouLookingFor/getTheUsersAreYouLookingFor";
import FriendsList from "./FriendsList"

const mapStateToProps = (state, ownProps) => {
   const currentCategory= state.main.userCategories.currentCategory;
   const myProfile = state.main.entities.users[state.main.myProfile];
   const allFriendsIds = myProfile.friends;
   const allFriends = allFriendsIds.map(friendId => state.main.entities.users[friendId]);
   const allUsersIds = state.main.result.users.filter(friendId => friendId !== state.main.myProfile);
   let allDisplayedUsers;
   if (currentCategory == "All friends") {
      allDisplayedUsers = allFriends;
   } else if (currentCategory == "Friends online") {
      allDisplayedUsers = allFriends.filter(friend => friend.online);
   } else if (currentCategory == "All users") {
      allDisplayedUsers = allUsersIds.map(userId => state.main.entities.users[userId]);
   }
   const searchText = state.main.friendSearch.searchText;
   const searchStatus = state.main.friendSearch.searchStatus;
   const allDisplayedUsersMap = fromJS(allDisplayedUsers);
   const searchedUsers = getTheFriendsAreYouLookingFor(allDisplayedUsersMap, searchText, searchStatus);
   const users = searchedUsers ? searchedUsers.toJS() : allDisplayedUsers;
   const sortedUsers = (users.length && users[0] !== "No matching friends found.") ? fromJS(users).sortBy(post => post.get("name")).toJS().slice(0).reverse()
   : users;

   return {
      state: {
         users: sortedUsers,
      }
   }
}

export default connect(mapStateToProps, null)(FriendsList)