import { connect } from "react-redux"
import getFormattedDate from "../../../../../lib/createCivilDate/getFormattedDate"
import PostItem from "./PostItem"

const mapStateToProps = (state, ownProps) => {
   const post = ownProps.post;
   return {
      state: {
         postAuthor: state.main.entities.users[post.author],
         icons: state.main.icons.postActivity,
         post: post,
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         getFormattedDate: (dateStr, page) => {
            return getFormattedDate(dateStr, page);
         },
         getImageId: image => image.split("/").join("").slice(0, -4),
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(PostItem);