import { fromJS } from "immutable";
import editAndCheckSearchText from "../editAndCheckSearchText";

const getSearchedUsers = (allUsersMap, editedAndCheckedSearchText) => {
   const allUsers = allUsersMap.toJS();
   const searchTextArr = editedAndCheckedSearchText.split(" ");
   let isThisTheWordYouAreLookingFor;
   if (searchTextArr.length > 2) return fromJS([]);

   const searchedUsers = allUsers.filter(user => {
      const userName = editAndCheckSearchText(user.name);
      const userNameArr = userName.split(" ");
      isThisTheWordYouAreLookingFor = false;
      userNameArr.find(namePart => {
         if (isThisTheWordYouAreLookingFor) return isThisTheWordYouAreLookingFor
         searchTextArr.find(searchTextWord => {
            isThisTheWordYouAreLookingFor = (
               editAndCheckSearchText(namePart).includes(searchTextWord)
               && !editAndCheckSearchText(namePart).indexOf(searchTextWord)
            ) ? true : false
            if (isThisTheWordYouAreLookingFor) return isThisTheWordYouAreLookingFor
         })
      });

      return isThisTheWordYouAreLookingFor
   })

   return fromJS(searchedUsers)
}

export default getSearchedUsers