import React from "react"
import Input from "../../../generic/Input"
import s from "./FriendSearch.module.css"
import HideIcon from "./HideIcon";
import Plug from "./Plug";

class FriendSearch extends React.Component {
   constructor(props) {
      super(props);
      this.friendSearchText = React.createRef();
   }

   cancelSearch = () => {
      this.props.callbacks.changeSearchText("");
      this.props.callbacks.changeSearchStatus();
   }

   componentDidMount() {
      this.cancelSearch()
   }

   inputHandler = () => {
      this.props.callbacks.changeSearchText(this.friendSearchText.current.value);
      this.props.callbacks.changeSearchStatus();
   }

   render() {
      return (
         <div className={`${s.friendSearch} friend-search`}>
            <div className={`${s.friendSearchWrapper} friend-search-wrapper`}>
               <form className={s.friendsForm}>
                  <div className={`${s.searchIcon}`}>
                     <img src={this.props.state.icons.searchIcon} alt="friend search icon" />
                  </div>

                  <Input
                     dispatch={this.props.dispatch}
                     inputState={this.props.state.inputState}
                     onInputHandler={() => {
                        this.inputHandler()
                     }}
                     value={this.props.state.text}
                     refContent={this.friendSearchText}
                  />
               </form>

               {this.props.state.icons.options.map((iconObj, i) => {
                  if (iconObj) {
                     return iconObj.role === "hide" ?
                        <HideIcon key={i} cancelSearch={this.cancelSearch} iconObj={iconObj} />
                        : <Plug key={i} />
                  } else {
                     return <Plug key={i} />
                  }
               }
               )}
            </div>
         </div>
      )
   }
}

export default FriendSearch