import React from "react";
import setPaddingRight from "../../../../lib/setPaddingRight";
import s from "./NoMatchingMessagesFound.module.css"

class NoMatchingMessagesFound extends React.Component {
   constructor (props) {
      super(props);
   }

   componentDidMount () {
      setPaddingRight()
   }

   render () {
      return (
         <div className={s.noMatchingMessagesFound}>
            <p>No matching messages found.</p>
         </div>
      )
   }
}

export default NoMatchingMessagesFound;