import React from "react";
import s from "./Chat.module.css";
import ChatEntryFieldContainer from "./ChatEntryFieldContainer";
import ChatFolderContainer from "./ChatFolderContainer";
import ChatHeaderContainer from "./ChatHeaderContainer";

class Chat extends React.Component {
   constructor(props) {
      super(props);
   }

   componentDidMount () {
      this.props.callbacks.setSidebarPositionAndSize("fixed", "Messages");
      this.props.callbacks.setPaddingRight();
   }
   
   componentWillUnmount () {
   }

   render () {
      return (
         <div className={`${s.chat} chat`}>
            <ChatHeaderContainer dialogId={this.props.dialogId}/>
            <ChatFolderContainer dialogId={this.props.dialogId}/>
            <ChatEntryFieldContainer dialogId={this.props.dialogId}/>
         </div>
      )
   }
} 

export default Chat