import React from 'react';
import Input from '../../../../generic/Input';
import HideIcon from './HideIcon';
import Icon from './Icon';
import s from './MessageSearch.module.css';
import Plug from './Plug';

class MessageSearch extends React.Component {

   constructor(props) {
      super(props);
      this.messageSearchText = React.createRef();
   }

   cancelSearch = () => {
      this.props.callbacks.changeSearchText("");
      this.props.callbacks.changeSearchStatus();
   }

   componentDidMount() {
      this.cancelSearch()
   }

   inputHandler = () => {
      this.props.callbacks.changeSearchText(this.messageSearchText.current.value);
      this.props.callbacks.changeSearchStatus();
   }

   getIconId = (icon) => this.props.callbacks.getIconId(icon)

   render() {
      return (<div className={`${s.messageSearch} message-search`}>
         <div className={`${s.messageSearchWrapper} message-search-wrapper`}>
            <form className={s.messagesForm}>
               <div className={`${s.searchIcon}`}>
                  <img src={this.props.state.icons.searchIcon} alt="message search icon" />
               </div>

               <Input
                  dispatch={this.props.dispatch}
                  inputState={this.props.state.inputState}
                  onInputHandler={() => {
                     this.inputHandler()
                  }}
                  value={this.props.state.text}
                  refContent={this.messageSearchText}
               />
            </form>

            {this.props.state.icons.options.map((iconObj, i) => {
               if (iconObj) {
                  return iconObj.role === "hide" ?
                  <HideIcon key={this.getIconId(iconObj.icon)} cancelSearch={this.cancelSearch} iconObj={iconObj} />
                  : <Icon key={this.getIconId(iconObj.icon)} iconObj={iconObj} />
               } else {
                  return <Plug key={i}/>
               }
            }
            )}
         </div>
      </div>)
   }

}

export default MessageSearch