import { connect } from "react-redux"
import { setUserCategoryActionCreator } from "../../../redux/actionCreators/mainActionCreators"
import Friends from "./Friends"

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      unmountHandler: () => {
         const action = setUserCategoryActionCreator("All friends")
         dispatch(action)
      }
   }
}

export default connect(null, mapDispatchToProps)(Friends)