import { connect } from "react-redux"
import ButtonsItem from "./ButtonsItem"

const mapStateToProps = (state, ownProps) => {
   return {
      state: ownProps.attributes,
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      getActivityHTML: (value, className) => {
         return value === 0 ? "" : <div className={`${className}`}>{value}</div>
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(ButtonsItem)