import React from "react"
import UserInfoItem from "./UserInfoItem"

class UserInfo extends React.Component {
   constructor (props) {
      super(props);
      this.state = {
         profileParameters: ["location", "education", "dateOfBirth", "email"],
         profileParametersNames: {
            "location": "Location",
            "education": "Education",
            "dateOfBirth": "Date of birth",
            "email": "E-mail",
         }
      }
   }

   render () {
      return (
         <div className="user-info">
            {this.state.profileParameters.map(
               parameter => (
                  <UserInfoItem key={parameter} parameter={this.state.profileParametersNames[parameter]} value={this.props.myProfile[parameter]} />
               )
            )}
         </div>
      )
   }

}


export default UserInfo