import React from "react";
import s from "./Dialogs.module.css"
import DialogsItemContainer from "./DialogsItemContainer";
import NoMatchingMessagesFound from "./NoMatchingMessagesFound";
import SendFirstMessage from "./SendFirstMessage";

class Dialogs extends React.Component {

   constructor(props) {
      super(props);
   }

   getDialogKey = (id, date) => this.props.getDialogKey(id, date)

   render() {
      if (!this.props.state.dialogs.length) {
         return (<SendFirstMessage />)
      }

      // state
      if (this.props.state.dialogs.length === 1 && this.props.state.dialogs[0] === "No matching messages found.") {
         return (<NoMatchingMessagesFound />)
      }

      return (
         <div className={s.dialogs}>
            {this.props.state.dialogs.slice(0).reverse().map((dialog, i) => {
               return (
                  <DialogsItemContainer
                     key={this.getDialogKey(dialog.id, dialog.messages[dialog.messages.length - 1].date)}
                     countedProps={
                        {
                           dialogNumber: i,  // *
                           lastNumber: this.props.state.dialogs.length - 1,   // *
                        }
                     }
                     dialog={dialog}
                  />
               )
            })
            }
         </div>
      )
   }


}

export default Dialogs