import { connect } from "react-redux"
import { addNewMessageActionCreator, changeDialogTextActionCreator } from "../../../redux/actionCreators/mainActionCreators"
import ChatEntryField from "./ChatEntryField"

const mapStateToProps = (state, ownProps) => {
   const myProfile = state.main.myProfile;

   return {
      state: {
         icons: state.main.icons.chat,
         person: myProfile,
         textareaState: {
            style: {
               margin: "1.5rem 0",
               height: "1.5rem",
               width: "100%",
            },
            placeholder: "What's happening?",
         },
      },
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         sendMessage: (text, person) => {
            const dialogId = ownProps.dialogId;
            let action = addNewMessageActionCreator(dialogId, text, person);
            dispatch(action);
         },
         changeWindowSize: elem => {
            if (elem.scrollHeight < 165) {
               elem.style.height = "0px";
               elem.style.height = `${elem.scrollHeight}px`;
               elem.style.overflow = "hidden"
            } else {
               elem.style.overflow = "auto"
            }
         }
      },
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChatEntryField);