import { connect } from "react-redux";
import HeaderSearch from "./HeaderSearch";


const mapStateToProps = state => {
   return {
      searchIcon: state.header.headerSearchIcon,
      inputState: {
         placeholder: "Search",
         style: {
            padding: "0 0.7rem",
            fontSize: "var(--main-fz)",
         },
      }
   }
}

const HeaderSearchContainer = connect(mapStateToProps, null)(HeaderSearch)

export default HeaderSearchContainer