import Profile from './Profile/Profile';
import s from "./Main.module.css";
import adaptive from './MainAdaptive.module.css'
import Messages from './Messages/Messages';
import { Route, Switch, Redirect } from 'react-router-dom';
import ErrorNotFound from './Error/ErrorNotFound';
import Friends from './Friends/Friends';
import ChatContainer from './MessagesChat/ChatContainer';
import FriendsContainer from './Friends/FriendsContainer';

const Main = ({ dialogsIds }) => (
   <main className={`${s.main} ${adaptive.main} main`}>
      <Switch>
         <Route exact path="/Profile">
            {/* // ! */}
            <Profile />
         </Route>
         <Route exact path="/Messages">
            <Messages />
         </Route>
         <Route exact path="/Friends/">
            <FriendsContainer />
         </Route>

         {dialogsIds.slice(0).reverse().map((dialogId, index) => (
            <Route key={dialogsIds} path={`/Messages/${dialogId}`}>
               <ChatContainer key={dialogId} dialogId={dialogId} />
            </Route>
         )
         )}

         <Route exact path="/">
            <Redirect to="/Profile" />
         </Route>

         <Route>
            <ErrorNotFound />
         </Route>
      </Switch>
   </main>
)

export default Main