import React from "react"
import setPaddingRight from "../../../../lib/setPaddingRight";
import s from "./FindFriends.module.css"

class FindFriends extends React.Component {
   constructor (props) {
      super(props);
   }

   componentDidMount () {
      setPaddingRight();
   }

   render () {
      return (
         <div className={s.findFriends}>
            Find friends in the section "All users"
         </div>
      )
   }
}

export default FindFriends