import { connect } from "react-redux"
import { setSubscriptionStatusActionCreator } from "../../../redux/actionCreators/sidebarActionCreators"
import WhoToFollowItem from "./WhoToFollowItem"

const mapStateToProps = (state, ownProps) => {
   return {
      state: {
         community: state.sidebar.communities[ownProps.communityId],
         myProfileId: state.sidebar.myProfile,
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         followButtonClickHandler: (community, myProfile) => {
            const status = community.subscribers.includes(myProfile) ? true : false;
            const action = setSubscriptionStatusActionCreator(status, community.id);
            dispatch(action);
         },
         getFollowButtonText: (community, myProfile) => {
            return community.subscribers.includes(myProfile) ?
               "Unfollow"
               : "Follow";
         }
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(WhoToFollowItem)