import { fromJS } from "immutable";
import { connect } from "react-redux";
import getTheMessagesAreYouLookingFor from "../../../../lib/getTheMessagesAreYouLookingFor/getTheMessagesAreYouLookingFor";
import Dialogs from "./Dialogs";


const mapStateToProps = (state, ownProps) => {
   // Отрисовка в порядке даты последнего сообщения
   const myProfile = state.main.entities.users[state.main.myProfile]
   const searchText = state.main.messenger.searchText;
   const searchStatus = state.main.messenger.searchStatus;
   const mainStateMap = fromJS(state.main);
   const searchedMessages = getTheMessagesAreYouLookingFor(mainStateMap, searchText, searchStatus);
   const dialogsIds = myProfile.dialogs;
   const allDialogs = dialogsIds.map(dialogId => state.main.entities.dialogs[dialogId]);
   const dialogs = searchedMessages ? searchedMessages.toJS() : allDialogs;
   const getTime = time => new Date(time).getTime();
   const sortedDialogs = (dialogs.length && dialogs[0] !== "No matching messages found.") ? fromJS(dialogs).sortBy(dialog => getTime(dialog.get("messages").toJS().slice(0).reverse()[0].date)).toJS()
      : dialogs;

   return {
      state: {
         dialogs: sortedDialogs,
         searchText: searchText,
         searchStatus: searchStatus,
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      getDialogKey: (id, date) => `${id}${date}`,
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dialogs);