import s from './ActualItem.module.css'

const ActualItem = ({tag}) => {
   return (<div className={`${s.actualItem} actual-item`}>
      <div className={`${s.place} actual-place`}>{`Popular in ${tag.place}`}</div>
      <div className={`${s.trend} actual-trend`}>{tag.trend}</div>
      <div className={`${s.value} actual-value`}>{`${tag.value} posts`}</div>
   </div>);
}

export default ActualItem