import { connect } from "react-redux"
import { changeFriendSearchStatusActionCreator, changeFriendSearchTextActionCreator } from "../../../../redux/actionCreators/mainActionCreators";
import FriendSearch from "./FriendSearch";

const mapStateToProps = (state, ownProps) => {
   const searchStatus = state.main.friendSearch.searchStatus;
   const icons = state.main.icons.search;

   return {
      state: {
         icons: {
            searchIcon: icons.mainSearchIcon,
            options: searchStatus ?
               [false, {icon: icons.hideIcon, role: "hide"}]
               : [false, false],
         },
         text: state.main.friendSearch.searchText,
         searchStatus: searchStatus,
         inputState: {
            placeholder: "Search",
            style: {
               padding: "0 0.2rem",
               fontSize: "var(--main-fz)",
            }
         }
      }
   }
}

const mapDispatchToProps = (dispatch, ownProps) => {
   return {
      callbacks: {
         changeSearchText: text => {
            let action = changeFriendSearchTextActionCreator(text);
            dispatch(action);
         },
         changeSearchStatus: () => {
            let action = changeFriendSearchStatusActionCreator();
            dispatch(action);
         },
      }
   }
}

export default connect(mapStateToProps, mapDispatchToProps)(FriendSearch);