import { fromJS } from "immutable";
import editAndCheckSearchText from "../editAndCheckSearchText";

const getSearchedMessages = (searchDialogsMap, editedAndCheckedSearchText) => {
   const searchedMessages = [];
   const searchedDialogs = searchDialogsMap.toJS();

   searchedDialogs.forEach(dialog => {
      dialog.messages.forEach((message) => {
         if (editAndCheckSearchText(message.text)
            .includes(editedAndCheckedSearchText)) {
            let messageObj = { ...message };
            let dialogObj = {
               ...dialog,
               messages: [messageObj],
            };
            searchedMessages.push(dialogObj);
         }
      })
   });

   return fromJS(searchedMessages)
}

export default getSearchedMessages