import React from "react";
import s from "./UserCategeoryButton.module.css"

class UserCategeoryButton extends React.Component {
   constructor (props) {
      super(props);
   }

   getbuttonActiveClassname = renderSelectedComponent => renderSelectedComponent ? "active": "notActive";

   clickHandler = categoryTitle => {
      this.props.callbacks.setUserCategory(categoryTitle);
      this.props.callbacks.startChangingTheCategoryOfUsers();
   }

   render () {
      return (
         <div className={s.userCategeoryButton}>
            <button className={`${s.button} ${s[this.getbuttonActiveClassname(this.props.state.renderSelectedComponent)]}`} onClick={() => this.clickHandler(this.props.state.categoryTitle)}>
               {this.props.state.categoryTitle}
            </button>
         </div>
      )
   }
}

export default UserCategeoryButton;