import React from "react";
import setPaddingRight from "../../../../lib/setPaddingRight";
import s from "./NoMatchingFriendsFound.module.css"

class NoMatchingFriendsFound extends React.Component {
   constructor(props) {
      super(props);
   }

   componentDidMount() {
      setPaddingRight();
   }

   render() {
      return (
         <div className={s.noMatchingFriendsFound}>
            No matching friends found.
         </div>
      )
   }
} 

export default NoMatchingFriendsFound