import s from './YouCanKnowThemItem.module.css'
import adaptive from '../SidebarsAdaptive.module.css'
import Avatar from '../../generic/Avatar'

const YouCanKnowThemItem = ({ youCanKnowThemItem }) => {
   const style = {
      gridArea: "avatar",
      justifySelf: "center",
      alignSelf: "center",
      transitionDuration: "0.03s",
      transitionProperty: "width padding",
      // width и height определяются в App.scss
      // т.к. их надо переопределять при событии hover
   }

   return (
      <div className={`${s.gridContainer} ${adaptive.gridContainer} friends-online-item`}>
         <Avatar src={youCanKnowThemItem.avatar} wrapperStyle={style} additionalClass={"friends-online-person-avatar"} />
         <div className={s.name}>{youCanKnowThemItem.name}</div>
         {youCanKnowThemItem.online ?
            (<div className={s.online}>online</div>)
            : (<div className={s.offline}>offline</div>)}
      </div>
   )
}

export default YouCanKnowThemItem