import React from 'react'
import { NavLink } from 'react-router-dom'
import setPaddingRight from '../../../../lib/setPaddingRight';
import setSidebarPositionAndSize from '../../../../lib/setSidebarPosition/setSidebarPositionAndSize';
import Avatar from '../../../generic/Avatar';
import s from './DialogsItem.module.css'

class DialogsItem extends React.Component {
   constructor(props) {
      super(props);
   }

   componentDidMount() {
      if (this.props.countedProps.dialogNumber === this.props.countedProps.lastNumber) {
         setSidebarPositionAndSize("fixed", "Messages");
         setPaddingRight();
      };
   }

   getFormattedDate = dateStr => this.props.callbacks.getFormattedDate(dateStr)

   trimMessage = message => this.props.callbacks.trimMessage(message)

   dialogClickHandler = () => {
      if (!this.props.state.messenger.searchStatus) return;
      this.props.callbacks.saveSoughtMessage({
         dialogId: this.props.state.dialog.id,
         message: {
            date: this.props.state.dateStr,
            text: this.props.state.lastMessage,
         },
         soughtMessageObj: this.props.state.messenger.soughtMessage
      })
   }

   render() {
      return (
         <NavLink to={`/Messages/${this.props.state.dialog.id}`}>
            <div className={`${s.dialogsItem} messages-dialogs-item`} onClick={this.dialogClickHandler}>
               <Avatar src={this.props.state.interlocutor.avatar} wrapperStyle={{width: "5rem", height: "5rem", gridArea: "avatar"}} />
               <div className={s.personName}>
                  {this.props.state.interlocutor.name}
               </div>
               <div className={s.personMessage}>
                  {this.trimMessage(this.props.state.lastMessage)}
               </div>
               <div className={s.messageDate}>
                  {this.getFormattedDate(this.props.state.dateStr)}
               </div>
            </div>
         </NavLink>
      )
   }
}

export default DialogsItem