import { fromJS } from "immutable";
import editAndCheckSearchText from "../editAndCheckSearchText";
import getSearchedDialogs from "./getSearchedDialogs";
import getSearchedMessages from "./getSearchedMessages";

const getTheMessagesAreYouLookingFor = (stateMap, searchText, searchStatus) => {
   if (!searchStatus) return false;

   const editedAndCheckedSearchText = editAndCheckSearchText(searchText);
   const mainState = stateMap.toJS();
   const myProfile = mainState.entities.users[mainState.myProfile];
   const allDialogsIds = myProfile.dialogs;
   const allDialogs = allDialogsIds.map(dialogId => mainState.entities.dialogs[dialogId]);
   let searchedDialogs;
   let searchedMessages;

   // Получение диалогов, которые включают искомые сообщения
   searchedDialogs = getSearchedDialogs(fromJS(allDialogs), editedAndCheckedSearchText).toJS();

   // Получение самих сообщений. Они сохраняются в виде диалогов,
   // каждый из которых содержит по одному этому сообщению
   searchedMessages = getSearchedMessages(fromJS(searchedDialogs), editedAndCheckedSearchText).toJS();

   if (searchedMessages.length === 0) {
      searchedMessages = ["No matching messages found."];
   }

   return fromJS(searchedMessages)
}

export default getTheMessagesAreYouLookingFor