import { Route, Switch, } from 'react-router'
import Controls from './Controls/Controls'
import YouCanKnowThemContainer from './FriendsOnline/YouCanKnowThemContainer'
import Plug from './Plug/Plug'
import s from './Sidebars.module.css'
import adaptive from './SidebarsAdaptive.module.css'
import WhatToFollow from './WhatToFollow/WhatToFollow'

const Sidebars = () => (
   <div className={`${s.sidebars} ${adaptive.sidebars} sidebars`}>
      <Controls/>
      <Switch>
         <Route path="/Messages">
            <YouCanKnowThemContainer />
         </Route>
         <Route exact path="/Profile">
            <WhatToFollow/>
         </Route>
         <Route exact path="/Friends">
            <Plug/>
         </Route>
      </Switch>
   </div>
)

export default Sidebars