import React from "react";
import ChatContinuationMessage from "./ChatContinuationMessage";
import ChatStartMessage from "./ChatStartMessage";

class ChatMessage extends React.Component {
   constructor(props) {
      super(props);
   }

   componentDidMount() {
      if (this.props.state.lastMessage) {
         this.props.callbacks.callChatFolderScroll();
         this.setStyleForSoughtMessage();
      }
   }

   setStyleForSoughtMessage = () => {
      if (this.props.state.searchStatus) {
         this.props.callbacks.setStyleForSoughtMessage();
      }
   }

   render() {
      if (this.props.state.renderMessage === "start" || this.props.state.time || this.props.state.changeAuthor) {
         return (<ChatStartMessage state={this.props.state.startMessageState}/>)
      } else if (this.props.state.renderMessage === "continuation") {
         return (<ChatContinuationMessage state={this.props.state.continuationMessageState}/>)
      }
   }
}

export default ChatMessage