import { fromJS } from "immutable";
import editAndCheckSearchText from "../editAndCheckSearchText";
import getSearchedUsers from "./getSearchedUsers";

const getTheUsersAreYouLookingFor = (allDisplayedUsersMap , searchText, searchStatus) => {

   if (!searchStatus) return false;

   const editedAndCheckedSearchText = editAndCheckSearchText(searchText);
   const allDisplayedUsers = allDisplayedUsersMap.toJS()
   let searchedFriends;

   searchedFriends = getSearchedUsers(fromJS(allDisplayedUsers), editedAndCheckedSearchText).toJS();

   if (searchedFriends.length === 0) {
      searchedFriends = ["No matching friends found."];
   }

   return fromJS(searchedFriends);
}

export default getTheUsersAreYouLookingFor;